<template>
  <div class="reset-password">
    <nav-head title="重置密码">
      <span slot="right" class="question-icon nav-right" @click="saveEdit">保存</span>
    </nav-head>
    <div class="padding-0-25">
      <div>
        <input type="text" minlength="6" maxlength="18" v-model="old_password" placeholder="请输入旧密码">
        <span @click="clearValue('old_password')" class="icon-del" v-bind:class="{'show': old_password }"></span>
      </div>
      <div>
        <input type="text" minlength="6" maxlength="18" v-model="password" placeholder="请输入新密码">
        <span @click="clearValue('password')" class="icon-del" v-bind:class="{'show': password }"></span>
      </div>
      <div>
        <input type="text" minlength="6" maxlength="18" v-model="confirm_password" placeholder="再次输入新密码">
        <span @click="clearValue('confirm_password')" class="icon-del" v-bind:class="{'show': confirm_password }"></span>
      </div>
    </div>
  </div>
</template>
<script>
import NavHead from '@/components/NavHead.vue'
import { Button, Toast } from 'mint-ui'
import { mapActions } from 'vuex'
import { RESET_PASSWORD } from '@/vuex/Mine'

export default {
  name: 'resetPassword',
  data () {
    return {
      old_password: '',
      confirm_password: '',
      password: ''
    }
  },
  methods: {
    saveEdit () {
      if (!this.old_password) {
        Toast('请输入旧密码!')
        return
      }
      if (!this.password) {
        Toast('请输入新密码!')
        return
      }
      if (!this.confirm_password) {
        Toast('请再次输入新密码!')
        return
      }
      if (this.password !== this.confirm_password) {
        Toast('两次密码输入不一致!')
        return
      }
      this.resetPassword({
        old_password: this.old_password,
        password: this.password,
        confirm_password: this.confirm_password
      })
    },
    clearValue (v) {
      this[v] = ''
    },
    ...mapActions({
      resetPassword: RESET_PASSWORD
    })
  },
  components: {
    NavHead,
    [Button.name]: Button
  }
}
</script>
<style scoped lang="scss">
  div.reset-password {
    text-align: center;
    min-height: 100%;
    position: relative;
    width: 100%;
    background-color: $background-light-color;
    > div > div {
      position: relative;
      > input {
        border: none;
        outline: none;
        width: 100%;
        font-size: 16px;
        height: 56px;
        line-height: 56px;
        margin-bottom: 10px;
        background: none;
        border-bottom: 1px solid $border-color;
        border-radius: 0;
        color: $font-color;
      }
      > span.icon-del {
        position: absolute;
        right: 0;
        top: 15px;
        @if $IF365 { background-image: url("../../../../assets/mine/ic_del_365@2x.png"); }
        @else { background-image: url("../../../../assets/mine/ic_del@2x.png"); }
        display: none;
      }
      > span.icon-del.show {
        display: inline-block;
      }
    }
  }
  .padding-0-25 {
    padding: 0 25px;
  }
</style>
